  
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*, *::before, *::after{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

main {
	margin: 0 auto;
	max-width: 90%;
  padding: 50px 30px;
}
:root {
  --primary: #8c54ff;
  --secondary: #F3A847;
  --tertiary: #B12704;
  --light: #fafafa;
  --background: #d8d8d8;
  --darker: #111111;
  --error: rgb(228, 46, 1);
  --spacing-one: calc(1rem * 0.5);
  --spacing-two: calc(1rem);
  --spacing-five: calc(2.5rem);

}

* {
  box-sizing: border-box;
}

html,
body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: var(--background);
  color: var(--light);
  font-family: 'Poiret One', cursive;

}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin-top: 0;
}

a {
  color: var(--tertiary);
  text-decoration: none;
  font-weight: bold;
}

a:hover {
  text-decoration: underline;
}

header {
  background: var(--background);
}

header a {
  color: var(--light);
}

button {
  font-size: 1rem;
  text-decoration: none;
  padding: 0.25rem 1rem;
  border-radius: .5rem;
  border: none;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: var(--secondary);
}

button:hover {
  opacity: .8;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role="img"] {
  cursor: default;
}

form {
  width: 40%;
}

input {
  font-size: 1rem;
  padding: 0.25rem;
  border: 1px solid var(--background);
  outline: none;
  border-radius: 3px;
  transition: box-shadow 0.2s;
}

input:focus {
  border: 1px solid var(--secondary);
  box-shadow: 0 0 .25rem rgba(0, 0, 0, .5);
}

li {
  list-style: none;
}

nav a:hover {
  text-decoration: none;
}

nav  li {
  cursor: pointer;
}

nav {
  color: rgb(255, 255, 255);
  vertical-align: middle;
  margin: 10px;
  font-weight: 500;
  font-size: large;
}

textarea {
	width: 600px;
	height: 120px;
	border: 3px solid #cccccc;
	padding: 5px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.myflex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.mymx-1 {
  margin-right: var(--spacing-one);
  margin-left: var(--spacing-one);
}

.mx-2 {
  margin-right: var(--spacing-two);
  margin-left: var(--spacing-two);
}

.my-1 {
  margin-top: var(--spacing-one);
  margin-bottom: var(--spacing-one);
}

.my-2 {
  margin-top: var(--spacing-two);
  margin-bottom: var(--spacing-two);
}

.my-5 {
  margin-top: var(--spacing-five);
  margin-bottom: var(--spacing-five);
}

.mypx-1 {
  padding-right: var(--spacing-one);
  padding-left: var(--spacing-one);
}

.px-2 {
  padding-right: var(--spacing-two);
  padding-left: var(--spacing-two);
}

.py-1 {
  padding-top: var(--spacing-one);
  padding-bottom: var(--spacing-one);
}

.py-2 {
  padding-top: var(--spacing-two);
  padding-bottom: var(--spacing-two);
}

.card {
  width: 25%;
  text-align: center;
}

.img-thumbnail {
  width: auto;
  height: 300px;
  margin-bottom: 50px;
  cursor: pointer;
}

.modalBackdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: auto;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modalContainer {
	background: var(--darker);
	border-radius: 5px;
	max-width: 100%;
	margin: 50px auto;
	padding: 15px;
	width: 960px;
}

.modalTitle {
	font-size: 18px;
	font-weight: 600;
	margin-bottom: 25px;
}

button {
	background: var(--primary);
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 16px;
	font-weight: 600;
	margin-bottom: 30px;
	padding: 10px 15px;	
}

a {
	border-radius: 5px;
	color: var(--primary);
	font-weight: 600;
	padding: 2px 4px;
	margin-left: -2px;
	margin-right: -2px;
	
}

.navActive {
  color: var(--secondary);
}

.myheaderBar {
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  background: rgb(120,11,40);
background: linear-gradient(90deg, rgba(120,11,40,1) 63%, rgba(255,0,0,1) 100%);
}

.footer {
  text-align: center;
  padding-top: 20px;
  padding-bottom: 10px;
  background: rgb(120,11,40);
  background: linear-gradient(90deg, rgba(120,11,40,1) 63%, rgba(255,0,0,1) 100%);
  border-top: 1px black solid;
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100px;
}

.page-container {
  position: relative;
  min-height: 100vh;
}

.portfolio-container {
  padding-bottom: 100px;
}

.footer-link {
  color: rgb(255, 255, 255);
  font-size: 50px;
  text-align: center;
  padding-left: 20px;
  padding-right: 20px;
}

.footer-link:hover {
  color: rgba(148,216,255,1);
  text-decoration: none;
}

.section-header {
  color:rgba(0, 11, 167, 0.5)
}

.myphoto {
  max-width: 100%;
  height: 600px;
  margin-bottom: 0;
  padding-top: 10px;
}

.myphoto-container {
  text-align: center;
  margin-right: 10px;
}

.about-padding {
  margin-right: 100px;
  color: #111111;
  font-family: Verdana,Geneva,Tahoma,sans-serif;
  font-weight: 100;
}

.myproject-container {
  padding: 20px;
  width: 100%;
}

.projectPhoto {
  height: 300px;
  border: solid 1px black;
  object-fit: contain;
  width: 100%;
  vertical-align:top
}

.projectPhoto:hover {
  background: black;
  -webkit-filter: blur(.5px);
  filter:opacity(65%);
}

.single-container {
  position: relative;
}

.single-container:hover .project-text {
  background: rgb(120,11,40);
  text-align: center;
  font-size: 36px;
}
.single-container:hover .project-description {
  display:initial;
  padding-left: 20px;
}

.project-text {
  position: absolute;
  bottom: 0px;
  left: 15px;
  background-color: rgb(148, 8, 8);
  color: white;
  padding-left: 20px;
  padding-right: 20px;
  font-size: 30px;
}

.project-description {
  display: none;
}

.project-text-container {
  top: 50px;
}


.download-container {
  text-align: center;
}

.download {
  text-align: center;
  font-size: 24px;
  color: rgba(8,35,148,1);
}

.download:hover {
  color: rgba(0,114,255,1);
  text-decoration: none;
}